@charset "utf-8";

.Kully{width: 1200px; margin: 50px auto 0px auto;}
.Select, li{list-style: none;}
.None{display: none;}
.MD{text-align: center;}
.Select{
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
}
a{color: #333; text-decoration: none;}
.Select li{
  color: #333;
  background: #F7F7F7;
  padding: 10px 15px;
  border-radius: 50px;
  text-align: center;
  font-size: 0.75rem;
  margin: 5px 20px;
}
.Select .pre{
  background: #5F0080;
}
.Select .pre a{color: #fff;}

/* 상품 */
.Goods{
  display: flex;
  justify-content: space-between;
  flex-wrap:wrap ;
  margin: 50px 0px;
}
.Goods li{width: 250px;}
.Goods li p{
font-size: 14px;
font-weight: 500;
}
.Goods li div{overflow: hidden; height: 260px; width: 250px;}
.Goods li div img{width: 250px; height: 260px; transition: 0.3s; border-radius: 5px;}
.Goods li div img:hover{transform: scale(1.02);}
.Goods li .Sale{font-weight: bold;}
.Goods li .Sale span{color: #f00; padding-right: 10px;}
.Goods li .Cost{text-decoration: line-through; color: #ccc; font-size: 12px;}
.Goods li .Carts{
  width: 18px;
  position: relative;
  top: -40px; right: -200px;
  border-radius: 50%;
  padding: 8px;
  text-align: center;
  color: #fff;
  background: #7F606C;
  cursor: pointer;
}

.Right{
  width: 40px;
  position: absolute;
  right: 17.5%;
  bottom: -13%;
  cursor: pointer;
}
/* 랜덤배너 */
#ran_banner{
  transform: translateX(32%);
  width: 1200px;}

/* 각 페이지 이미지 센터정렬 */
.page_image{
  transform: translateX(350px);
}
