.Login{
  width: 1200px;
  margin: 0 auto;
}
.Login_form{
  width: 600px;
  margin: 50px auto;
  text-align: center;
}
.Login_form > h2{color:#5F0080;}

.find{width: 800px;
  font-size:12px; margin: 10px 0px 20px 0px;}
.find a:first-child{border-right:1px solid #333 ; margin-right: 5px; padding-right: 5px;}
input[type='text']{width: 300px; border: 1px solid #ccc; padding: 15px 30px 15px 10px;}

