@charset "utf-8";

.food_family{
  width: 1200px; height: 600px;
  margin: 0 auto;
}

.food_title{
  text-align: center;
  margin-top: 50px;
}
.food_subtitle{
  text-align: center;
  color: #848f9a;
  font-size: 14px;
  margin: -10px 0px 20px 0px;
}

.food_family_ul{
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
  font-size: 12px;
  overflow-wrap: break-word;
  white-space: normal;
}
.food_family_ul > li{width: 250px; height: 260px;}
.food_family_ul > li > div{overflow: hidden;}
.food_family_ul > li > div > img{
  width: 250px;
  transition: 0.3s;
  border-radius: 5px;
}
.food_family_ul > li > div > img:hover{
  transform: scale(1.01);
  border-radius: 5px;
}
/* title 서식 */
.family_title{font-weight: 600;}
/* cost */
.family_cost{text-decoration-line: line-through; color: #ccc;}
.family_sale{font-weight: bold;}
.family_sale > span{color: #f00; margin-right: 10px;}

/* 담기 서식 */
.family_cart{
  text-align: center;
  font-size: 16px;
  border: 1px solid #ccc;
  border-radius: 5px;
  padding: 7px;
  cursor: pointer;
  background: #fff;
}

/* 쿠폰서식 */
.coupon{
  position: relative;
  top: 40px; left: 10px;
  color: #fff;
  background: #BD76FF;
  font-size: 14px;
  font-weight: 600;
  padding:6px 2px;
  text-align: center;
  width: 80px;
  border: 1px solid #BD76FF;
  border-radius: 5px;
  z-index: 1000;
}

/* 코멘트 서식 */
.comment{color: #ccc;}


/* Right 넘기는 서식 */
.Right2{
  position: relative;
  top: -100px;
  right: -1180px;
  width: 40px;
  cursor: pointer;
}

/* Best_rank 서식 */
.best_rank_ul{
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
  font-size: 12px;
  overflow-wrap: break-word;
  white-space: normal;
}
.best_rank_ul > li{width: 200px; height: 260px;}
.best_rank_ul > li > div{overflow: hidden;}
.best_rank_ul > li > div > img{
  width: 200px;
  transition: 0.3s;
  border-radius: 5px;
}
.best_rank_ul > li > div > img:hover{
  transform: scale(1.02);
  border-radius: 5px;
}


/* 랭킹 서식 */
.rank{
  font-size: 30px;
  font-weight: bold;
}
.rank > .family_title, .family_sale{
  font-size: 14px;
}

/* 이미지 아래 설명 서식 */
.text_div{
  background:#F3F3F3;
  border-radius: 0px 0px 5px 5px;
  margin-top: -5px;
  padding-bottom: 10px ;
  height: 150px;
}

.text_div > div{
  margin-left: 5px;
}

.title_sale{
  transform: translate(23.5px, -70px);
  width: 175px;
}

/*  Best_rank 담기 서식 */
.family_cart2{
  text-align: center;
  font-size: 16px;
  border: 1px solid #ccc;
  border-radius: 5px;
  padding: 7px;
  cursor: pointer;
  background: #fff;
  margin: 0px 10px;
  transform: translateY(-50px);
}

/* 장보기 특가 서식 */
.special{
  width: 50px;
  position: relative;
}
.special1{
  top: -40px; left: 10px;
}
.special2{
  top: -40px; left: 200px;
}

