@charset "utf-8";

.header{
  position: relative;
}
.Header_bottom{
  border-bottom: 1px solid #ccc;
  box-shadow: 0 4px 2px -2px rgba(192, 192, 192, 0.6);
  margin-bottom: 10px;
}
.Header_logo{
  width: 1200px;
  margin: 30px auto;
}
.Header_login{
  width: 1200px;
  margin: 0 auto;
  text-align: right;
}
.Header_login a{
  font-size: 11px;
  border-right:1px solid #ccc;
  padding-right: 5px;
}
.Header_login a:first-child{color: #5F0080;}
.Header_login a:nth-child(2){margin: 0px 10px;}
.Header_login a:last-child{border:none;}

/* 헤더 왼쪽 */
div .Header_logo img{
  transform: translate(-10px,10px);
}
div .Header_logo a{font-size: 18px;}
div .Header_logo .left1{
  margin-right:10px; 
  color: #5F0080;
  border-right:1px solid #ccc;
  padding-right: 10px;
}
div .Header_logo .left2{color: #ccc;}
div .Header_logo .left2:hover{color: #5F0080;}

/* 검색창 서식 */
.search_box{
  position: absolute;
  top: 55px;
  left: 50%;
  transform: translateX(-50%);
  border-radius: 5px;
  border: 1px solid #5F0080 !important;
}
.search{
  position: absolute;
  top: 68px;
  right:42%;
  font-size: 20px;
  color:#5F0080;
}

/* 헤더 오른쪽 서식 */
.Header_right{
  font-size: 30px;
  position: absolute;
  z-index: 1000;
  top: 55px;
  right: 18.5%;
}
.heart{
  margin: 0px 20px;
}
.heart:hover, .Map:hover, .right_cart:hover{color: #5F0080;}

/* 내비게이션 서식 */
/* 내비 카테고리 */
.nav_left{
  font-size: 16px;
  margin-left: 350px;
}
.bars{margin-right: 10px;}

/* 내비게이션 서식 */
.gnb{transform: translateY(-37px);}
.gnb > ul{
  width: 800px;
  margin: 0 auto;
  display: flex;
  justify-content: space-evenly;
}
.gnb > ul li a{
  font-weight: 500;
  border-bottom:2px solid transparent;
  padding-bottom: 3px;
}
.gnb > ul li:hover a{
  color: #5F0080;
  border-bottom: 1px solid #5F0080;
}

/* 내비 오른쪽 서식 */
.nav_right{
  font-size:12px;
  padding: 5px 10px;
  border: 1px solid #ccc;
  width: 100px;
  position: absolute;
  top: 125px;
  right: 18%;
  border-radius: 50px;
}
.nav_right a{font-weight: 500;}
.nav_right .delivery{
  color:#5F0080;
}
